body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html, body, #root, .App {
  width: 100%;
  height: 100%;
}

button {
  font-size: 1em;
  border-radius: 2px;
  padding: 0.5em 1em;
  border: 1px solid #ccc;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.floater {
  background-color: white;
  padding: 5px;
  position: fixed;
  top: 0;
  right: 0;
  width: 200px;
  border-bottom-left-radius: 3px;
  -webkit-box-shadow: 0 0 3px #2e3440;
          box-shadow: 0 0 3px #2e3440;
}

.floater > * {
  display: block;
  margin-top: 5px;
}
.floater > *:nth-child(1){
  margin-top: 0;
}

.artboard {
  -ms-flex-negative: 0;
      flex-shrink: 0;
}




